.list-container{
  display: flex;
  /*animation: slide 1s cubic-bezier(.53,.16,.43,.79) forwards;*/
}
.remove-animation{
  -webkit-animation: slide 1s cubic-bezier(.53,.16,.43,.79) forwards;
          animation: slide 1s cubic-bezier(.53,.16,.43,.79) forwards;
}
.list {
  margin-right: auto;
  color: #000000;
  border: 1px solid black;
  background-color: #cccccc;
  width: 80%;
  border-radius: 15px;
  padding: 10px;
  margin-bottom: 10px;
}
button{
  margin: 10px;
  height: 3em;
  align-self: center;
  border-radius: 15px;
  padding: 0px 1em;
  color: white;
  cursor: pointer;
  box-shadow: inset;
  border: none;
}
button:hover{
  background: #000000;
}
.done{
background-color: #00b359;
}
.remove{
  background-color:  #ff3333;
}
.wrapper{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: auto;
  width: 80%;
  height: 100%;
}
.search-box{
  width:100%;
  height: 3em;
  border-radius: 15px;
  margin: 10px 0px;
}
.input-box{
  width: 100%;
  border-radius: 15px;
  padding-top: 10px;
}
.Add-Button{
  background-color: #00b359;
  margin-left: 10px;
}
.text-container{
  display: flex;
}
.title-bar{
  display: flex;
}
.title-bar h1{
  margin: auto;
}
.linethrough {
  -webkit-text-decoration: line-through #000000 2px;
          text-decoration: line-through #000000 2px;
}
.list p:first-child{
 text-align: justify;
}
.App{
  padding: 2em;
  background-color: transparent;
}
.edit{
  background-color: blueviolet;
}
body{
  margin: 0px;
  background: linear-gradient(45deg, #000000, #4d0026);
}
.title-bar h1{
  color: white;
}
.list:hover, .search-box:hover, .input-box:hover {
  -webkit-animation: zoom 0.2s forwards;
          animation: zoom 0.2s forwards;
}

.button-container{
  display: flex;
  align-items: center;
  border: 1px solid black;
  background-color: #404040;
  border-radius: 15px;
  padding: 10px;
  margin-bottom: 10px;
  width: 15%;
  /*flex-wrap: wrap;*/
}

@-webkit-keyframes zoom {
  from{}
  to{-webkit-transform: scale(1.01);transform: scale(1.01); }
}

@keyframes zoom {
  from{}
  to{-webkit-transform: scale(1.01);transform: scale(1.01); }
}

@-webkit-keyframes slide {
  from{}
  to{-webkit-transform: translateX(calc(100% + 200px));transform: translateX(calc(100% + 200px))}
}

@keyframes slide {
  from{}
  to{-webkit-transform: translateX(calc(100% + 200px));transform: translateX(calc(100% + 200px))}
}
